import Vue from 'vue'
import '@badam/vue-tsx-support/enable-check'
import '@badam/vue-tsx-support/options/enable-nativeon'
import {
  calculateWindow,
  exec,
  ready,
  addWebpAvifSupport,
  initErrorReporter,
  reportSensors,
  metric,
  DayJs,
  TimeType,
  getPlatform,
  setLang,
  getServTs,
  appType,
  updateTsDelta,
  calculateWindow4 as calculateWindow41,
} from '@badam/common-utils'
import store from './store'
import { getArgs, isDisplayPC, initAppleLogin, pkg } from './utils'
import { name, nanoid } from '../../package.json'
import './index.module.scss'
import lang from './lang/index.ksa.json'
import Loading from './components/loading.vue'

export function calculateWindow4() {
  const height = Math.min(window.screen.height, window.innerHeight)
  if (height < 5) {
    setTimeout(() => {
      calculateWindow4()
    }, 16)
    return
  }
  const meta: any = document.getElementById('viewport-config')
  meta.name = 'viewport'
  meta.content =
    'width=device-width,initial-scale=1,user-scalable=no,maximum-scale=1,minimum-scale=1,viewport-fit=cover'
  document.documentElement.style.height = '100%'
}

export function mainInit() {
  console.warn('#ack page')
  Vue.config.productionTip = false
  Vue.component('LoadingComp', Loading)

  if (getArgs('lan') !== 'zh') {
    setLang(lang)
  } else {
    setLang({}, '', true)
  }

  if (isDisplayPC()) {
    document.body.classList.add('-pc')
  }

  async function setTime() {
    const time = await getServTs(appType.sawa)
    updateTsDelta(time)
  }

  function init() {
    return Promise.all([setTime()])
  }

  exec(async () => {
    calculateWindow4()
    banScale()

    try {
      init()
      DayJs.setOffset(TimeType.Ksa)
      reportSensors('act_meaningful_content_loaded', true)
    } catch (err) {
      console.error(err)
    }
  })

  function banScale() {
    document.documentElement.addEventListener(
      'touchstart',
      (event) => {
        if (event.touches.length > 1) {
          event.preventDefault()
        }
      },
      false
    )

    let lastTouchEnd = 0
    document.documentElement.addEventListener(
      'touchend',
      (event) => {
        const now = Date.now()
        if (now - lastTouchEnd <= 300) {
          event.preventDefault()
        }
        lastTouchEnd = now
      },
      false
    )
    document.addEventListener('gesturestart', (event) => {
      event.preventDefault()
    })
  }
}
