api.setWgetEnable(false)

const wget = api.get.bind(api)
const get = api.get.bind(api)
const post = api.post.bind(api)

export function resolveApiBase() {
  const base = getArgs('api_base')
  if (base) {
    const v = decodeURIComponent(decodeURIComponent(base))
    const u = new URL(v).host
    const u2 = u.slice(-1) === '/' ? u : `${u}/`
    return `${new URL(v).protocol}//${u2}`
  }
  return process.env.VUE_APP_ENV === 'development' || getArgs('is_debug')
    ? 'https://test02-pk.badambiz.com/'
    : 'https://gif.applecdn.cloud/'
}

export function setDefApiBase(b = '') {
  let base = resolveApiBase()
  console.warn('setDefApiBase', base)
  base = base.replace('http://', 'https://')
  api.getAxiosInstance().defaults.baseURL = b || base
}

// axios.defaults.headers.post['content-type'] = 'application/x-www-form-urlencoded'

export function loginWithApple(
  user_id: string,
  access_token: string,
  nickname?: string,
  auto_create = false
) {
  const auth_access = store.state.data
  const uuid = getArgs('uuid') || ''
  const bundle_id = getArgs('bundle_id') || ''
  const obj: any = {
    login_type: 'apple',
    user_id,
    access_token,
    nickname,
    auth_type: 1,
    auth_access,
    auto_create,
  }
  let data: any = new URLSearchParams()
  obj && Object.keys(obj).forEach((key) => data.append(key, obj[key]))
  // data = qs.stringify(data)

  return post('/api/user_mode/get_login_key/', data, {
    'x-cli-br': 1,
    'x-cli-id': uuid,
    'x-cli-pn': bundle_id,
    'content-type': 'application/x-www-form-urlencoded',
  } as any) as Promise<[boolean, ApiResult<any>]>
}

