export const addEvent = (event: BadamEvent) =>
  // if (conf.useMock) {
  //   return mock.commonRank()
  // }
  get('/api/sys/add_event/', {
    event_name: event.eventName,
    uuid: event.uuid,
    version: event.version,
    str_param2: event.dict.str_param2,
    str_param3: event.dict.str_param3,
    str_param4: event.dict.str_param4,
    str_param1: JSON.stringify({
      uid: event.uid,
      op: event.op,
      tk: event.tk,
      ua: event.ua,
      proj: event.proj,
      href: event.href,
      rid: event.rid,
    }),
    int_param1: event.dict.int_param1,
    int_param2: event.dict.int_param2,
    int_param3: event.dict.int_param3,
    int_param4: event.dict.int_param4,
  }) as unknown as Promise<[boolean, ApiResult<any>]>

/**
 *
 * 手机获取验证码
 * @export
 * @param {string} mobile 手机号码
 * @param {string} country_code 国家码
 * @return {*}
 */
export function getMobileCode(mobile: string | number, country_code: string | number) {
  const uuid = getArgsFromUrl('uuid') || ''
  const bundle_id = getArgsFromUrl('bundle_id') || ''
  const device_token = getArgsFromUrl('device_token') || ''
  let obj
  if (device_token) {
    obj = {
      mobile,
      country_code,
      device_token,
      from_app_uuid: uuid,
      from_app_package_name: bundle_id,
    }
  } else {
    obj = {
      mobile,
      country_code,
      from_app_uuid: uuid,
      from_app_package_name: bundle_id,
    }
  }
  return wget('/api/user/get_code/', obj, {
    'x-cli-br': 1,
  }) as Promise<[boolean, ApiResult<any>]>
}

export function verify(params: {
  mobile?: string | number
  contry_code?: string | number
  code?: string | number
  id_token?: string
  user_id?: string
  access_token?: string
  apple_code?: string
  channel?: string
  channel_values?: string[]
}) {
  const uuid = getArgs('uuid') || ''
  const bundle_id = getArgs('bundle_id') || ''
  const audit_type = getArgs('auth_type') || 1
  const auth_type = getAuthType(pkg)
  console.log('auth_type:', auth_type, pkg)
  return get<{ cancel_account_key: string }>(
    '/api/cancel_account/verify/',
    { ...params, audit_type, auth_type },
    {
      'x-cli-br': 1,
      'x-cli-id': uuid,
      'x-cli-pn': bundle_id,
      'x-cli-tk': getArgs('session_key'),
      'content-type': 'application/x-www-form-urlencoded',
    }
  )
}

export function revokeBalootCancel(session_key: string) {
  const uuid = getArgs('uuid') || ''
  const bundle_id = getArgs('bundle_id') || ''

  return get(
    '/api/cancel_account/revoke_baloot_cancel/',
    {
      session_key,
      device: 'web',
    },
    {
      'x-cli-br': 1,
      'x-cli-id': uuid,
    }
  ) as Promise<[boolean, ApiResult<any>]>
}
