
import { defineComponent } from 'vue'

export default defineComponent({
  props: ['onRef', 'initValues'],
  data() {
    return {
      show: false,
    }
  },
  created() {
    if (this.onRef) {
      this.onRef(this)
    }
    this.change(this.initValues)
  },
  watch: {
    initValues: {
      handler(newv, oldv) {
        this.change(newv)
      },
      deep: true,
    },
  },
  methods: {
    change(value: any) {
      for (const i in value) {
        ;(this as any)[i] = value[i]
      }
    },
  },
})
