import { handleApiResult, api, getArgsFromUrl } from '@badam/common-utils'
import axios from 'axios'
import { getArgs } from '@/src_bind/utils'
import FirebaseManager from './firebase-manager'
import { useLocalStorage } from './local-storage-manager'
import { LOCAL_STORAGE_KEY } from './local-storage-manager/consts'

const LOG_TAG = 'domain-dial'

export enum DialType {
  神策 = 1,
  API = 2,
}

export function setApiBaseUrl(url: string) {
  if (url) {
    if (getArgsFromUrl('is_debug')) {
      return
    }
    axios.defaults.baseURL = url
    api.getAxiosInstance().defaults.baseURL = axios.defaults.baseURL
  }
}

export async function getFirebaseConfig(): Promise<string | null> {
  return FirebaseManager.getInstance().getValueFromConfig('ow_conf')
}

const dial_timeout = 5000

export async function dailUrls(urls: string[], type: DialType): Promise<string> {
  const promise: Promise<string> = new Promise((r) => {
    for (let i = 0; i < urls.length; i += 1) {
      const url = urls[i]
      if (type === DialType.API) {
        dialGoPk(url).then((res) => {
          if (res) {
            r(url)
          }
        })
      } else if (type === DialType.神策) {
        dialSensors(url).then((res) => {
          if (res) {
            r(url)
          }
        })
      }
    }
  })
  const timeout: Promise<string> = new Promise((r) =>
    setTimeout(() => {
      r('')
    }, dial_timeout)
  )
  return Promise.race([promise, timeout])
}

function resolveApiBase() {
  const base = getArgs('api_base')
  if (base) {
    const v = decodeURIComponent(decodeURIComponent(base))
    const u = new URL(v).host
    const u2 = u.slice(-1) === '/' ? u : `${u}/`
    return `${new URL(v).protocol}//${u2}`
  }
  return process.env.VUE_APP_ENV === 'development' || getArgs('is_debug')
    ? 'https://test02-pk.badambiz.com/'
    : 'https://gif.applecdn.cloud/'
}

export async function dialGoPk(url: string): Promise<string> {
  const res = await api.get(`https://${url}/api/sys/h5_info`, {})
  return res[0] ? url : ''
}

export async function dial(urls: string[], type: DialType) {
  const ls = useLocalStorage()
  if (type === DialType.API) {
    const res = await dailUrls(urls, DialType.API)
    if (res) {
      ls.set(LOCAL_STORAGE_KEY.上次使用过的API_BASE, res)
      setApiBaseUrl(`https://${res}/`)
    } else {
      setApiBaseUrl(resolveApiBase())
    }
  }
}

export async function dialSensors(url: string): Promise<string> {
  const res = await api.get(`https://${url}/api/gm/hii`, {})
  return res[0] ? url : ''
}

export function dial2(go_pk: string[] | null) {
  const ls = []
  if (go_pk) {
    ls.push(dial(go_pk, DialType.API))
  }
  return Promise.all(ls)
}

export async function getConfigAndDial(): Promise<boolean> {
  const ls = useLocalStorage()
  const conf = await getFirebaseConfig()
  if (conf) {
    const o = JSON.parse(conf)
    const go_pk: string[] = o.go_pk
    console.log(LOG_TAG, 'conf', go_pk)
    ls.set(LOCAL_STORAGE_KEY.备用apilist, go_pk)
    await dial2(go_pk)
    return true
  } else {
    const go_pk = ls.get(LOCAL_STORAGE_KEY.备用apilist)
    await dial2(go_pk)
    return true
  }
}
