/* eslint-disable import/prefer-default-export */
import { ref } from 'vue'

function createLocalStorageManager() {
  const reactiveStorageMap = ref({} as any)

  function get(key: any): any | null {
    const value = reactiveStorageMap.value[key]
    if (!value) {
      const v = localStorage.getItem(key)
      try {
        if (v) {
          const value = JSON.parse(decodeURIComponent(atob(v)))
          reactiveStorageMap.value[key] = value
          return value
        }
      } catch (e) {
        // throwError({ err: '解key失败', data: key })
      }
      return null
    }
    return value
  }

  function set(key: any, value: any) {
    try {
      const v = btoa(encodeURIComponent(JSON.stringify(value)))
      localStorage.setItem(key, v)
      reactiveStorageMap.value[key] = value
    } catch (e) {
      // throwError({ err: 'set key-value失败', data: { key, value } })
    }
  }
  return function manager() {
    return {
      get,
      set,
    }
  }
}

export const useLocalStorage = createLocalStorageManager()
