import { getArgsFromUrlHash, getArgsFromUrl, matchExpr } from '@badam/common-utils'

export const resolveLudoStyle = () => {
  if (getArgsFromUrlHash('bundle_id') === 'com.sawa.ludo') {
    document.getElementsByTagName('html')[0].classList.add('-ludo')
  }
}

/// dark mode
const initSawaSDKFn = () => {
  let called = false
  return () => {
    if (called) {
      return
    }
    called = true
    if ((window as any).JavaBridgeObj && !(window as any).JavaBridgeObj.__exec__) {
      ;(window as any).JavaBridgeObj.__actions__ = {}
      ;(window as any).JavaBridgeObj.__id__ = 0
      ;(window as any).JavaBridgeObj.__exec__ = (id: any, respJSON: any) => {
        const handler = (window as any).JavaBridgeObj.__actions__[id]
        if (handler) {
          handler(respJSON)
        }
      }
    }
  }
}
export const initSawaSDK = initSawaSDKFn()
// dark mode
function applyColorTheme(darkMode: boolean) {
  if (darkMode) {
    // @Td Change StatusBarTheme
    document.getElementsByTagName('html')[0].classList.add('-dark')
  } else {
    document.getElementsByTagName('html')[0].classList.remove('-dark')
  }
}

const registerBridgeCbId = (cb: any) => {
  const id = ((window as any).JavaBridgeObj.__id__ += 1)
  ;(window as any).JavaBridgeObj.__actions__[id] = cb
  return id
}

async function getDarkMode() {
  let resolver: any = null
  const p = new Promise<boolean>((resolve) => {
    resolver = resolve
  })

  if (
    (window as any).JavaBridgeObj &&
    (window as any).JavaBridgeObj.isDarkMode
    // true
  ) {
    const callback_id = registerBridgeCbId(function (res: any) {
      console.warn('isDarkMode', res)
      if (res) {
        const { mode } = JSON.parse(res)
        resolver(mode === 1)
      }
    })
    console.warn('#1', callback_id)
    ;(window as any).JavaBridgeObj.isDarkMode(JSON.stringify({ callback_id }))
  } else {
    console.warn('NO API isDarkMode')
    resolver(false)
  }

  // resolver(true)
  return p
}

function isSafariDarkModeSupport() {
  const mode = getArgsFromUrl('mode')
  if (mode) {
    return true
  }
  return false
}

function enableDarkModeiOS() {
  const darkModeMediaQuery = window.matchMedia('(prefers-color-scheme: dark)')
  darkModeMediaQuery.addEventListener('change', (e) => {
    if (e.matches) {
      applyColorTheme(true)
    } else {
      applyColorTheme(false)
    }
  })
  if (darkModeMediaQuery.matches) {
    applyColorTheme(true)
  } else {
    applyColorTheme(false)
  }
}
export async function initDarkMode() {
  if (!isSafariDarkModeSupport()) return
  const mode = String(getArgsFromUrl('mode'))
  matchExpr(mode, [
    ['0', () => applyColorTheme(false)],
    ['1', () => applyColorTheme(true)],
    ['2', () => enableDarkModeiOS()],
  ])
}

export function setUrlParams(key: string, value: string | number | boolean | undefined) {
  const searchParams = new URLSearchParams(window.location.search)
  // 修改参数值
  if (value === undefined) {
    searchParams.delete(key)
  } else {
    searchParams.set(key, String(value))
  }

  const str = searchParams.toString()

  let newUrl
  if (str) {
    newUrl = `${window.location.pathname}?${str}`
  } else {
    newUrl = window.location.pathname
  }

  const hash = location.hash
  if (hash) {
    newUrl += hash
  }

  // window.history.replaceState({}, '', newUrl)

  return newUrl

  // 将修改后的参数添加到 URL 中
}

export function setUrlParams2(key: string, value: string | number | boolean | undefined) {
  let u = setUrlParams(key, value)
  u = u.replace(location.hash, '')
  return location.origin + u
}

export function refreshDeviceToken(
  mobile: number | null,
  country: string | null,
  flag: string | null
) {
  if (mobile) localStorage.setItem('ludo_refresh_device_token_mobile', `${mobile}` || '')
  if (country) localStorage.setItem('ludo_refresh_device_token_country', `${country}` || '')
  if (flag) localStorage.setItem('ludo_refresh_device_token_flag', `${flag}` || '')

  const host = 'sawaludo'
  let href = location.href
  href = setUrlParams2('is_refresh', 1)
  href = `${host}://refresh_device_token/?url=${encodeURIComponent(href)}`
  console.warn('refreshDeviceToken', href)
  window.location.href = href
}

// test, @Td remove
;(window as any).applyColorTheme = applyColorTheme
;(window as any).getDarkMode = getDarkMode

export const addUserScript = (src: string) =>
  new Promise((resolve) => {
    const script = document.createElement('script')
    script.src = src
    document.body.appendChild(script)
    script.onload = () => {
      resolve(true)
    }
    script.onerror = () => {
      resolve(false)
    }
  })

export async function loadVConsoleIfNeed() {
  if (process.env.VUE_APP_ENV === 'production') {
    // if (!getArgsFromUrl('log')) {
    return
  }
  const ret = await addUserScript('https://unpkg.com/vconsole/dist/vconsole.min.js')
  if (!ret) {
    return
  }
  const vConsole = new (window as any).VConsole()
}
