export function getMp4url(url: string) {
  const DOMAIN = window.location.hostname
  if (DOMAIN.indexOf('cdn-') === -1) {
    return url.replace('cdn-', '')
  }
  return url
}

// export const isLudo = getArgs('package') !== 'cn.sawalive.isawa'
export const pkg = getArgs('package')

export const isInLudoClient = isProductType(ProductType.Ludo)
export const isInDiwaniaClient = isProductType(ProductType.Diwania)

export function getPkgClientId(pkg: string) {
  const channel = getArgs('channel')
  if (channel === 'hk.longchiu.dwn.h5') {
    return {
      apple: 'hk.longchiu.dwn.h5',
      facebook: '7953232924746525',
      client: 'diwania',
      snapchat: '',
    }
  }
  // baloot ksa ,baloot ludo [new add]
  if (pkg === 'cn.sawalive.isawa') {
    return {
      apple: 'cn.sawalive.isawa',
      facebook: '391521625027907',
      client: 'baloot',
      snapchat: '',
    }
  } else if (pkg === 'com.sawa.ludo') {
    return {
      apple: 'com.sawa.ludoh5',
      facebook: '462662855828218',
      client: 'ludo',
      snapchat: '',
    }
  } else if (pkg === 'hk.longchiu.dwn.h5') {
    return {
      apple: 'hk.longchiu.dwn.h5',
      facebook: '7953232924746525',
      client: 'diwania',
      snapchat: '',
    }
  } else if (pkg === 'com.sawa.weyay.h5') {
    return {
      apple: 'com.sawa.weyay.h5',
      facebook: '1666468373982706',
      client: 'weyay',
      snapchat: '',
    }
  }
  return {
    apple: 'com.sawa.ksah5',
    facebook: '297601757621370',
    client: 'sawa',
    snapchat: getArgs('is_debug')
      ? 'bc0b6d26-9d50-4a7a-bb20-9a8ccfdbe509'
      : '55071d93-cf78-495a-a45e-04564b36962c',
  }
}

export function getAuthType(pkg: string) {
  const channel = getArgs('channel')
  const p = resolvePage()
  if (pkg === 'cn.sawalive.isawa') {
    return 1
  } else if (pkg === 'com.sawa.ludo' || (pkg === 'com.sawa.ksa' && p === PageEnum.Unregister)) {
    return 2
  } else if (pkg === 'hk.longchiu.dwn.h5' || channel === 'hk.longchiu.dwn.h5') {
    return 4
  }
  return 1
}

export async function initSnapchatSDK(id: string, resultCb: any, errCb: any) {
  ;(window as any).snapKitInit = function () {
    /* Add your code here */
    attachSnapchat(id, resultCb, errCb)
  }
  ;(function (d, s, id) {
    let js: any
    let sjs: any = d.getElementsByTagName(s)[0]
    if (d.getElementById(id)) return
    js = d.createElement(s)
    js.id = id
    js.src = 'https://sdk.snapkit.com/js/v1/login.js'
    sjs.parentNode.insertBefore(js, sjs)
  })(document, 'script', 'loginkit-sdk')
}

export async function attachSnapchat(
  id: string,
  resultCb: (result: any) => void,
  errCb: (err: any) => void
) {
  let loginButtonIconId = id
  // Mount Login Button
  const snap = (window as any).snap
  snap.loginkit.mountButton(loginButtonIconId, {
    clientId: getPkgClientId(pkg).snapchat,
    redirectURI: window.location.href.split('?')[0],
    scopeList: ['user.display_name', 'user.bitmoji.avatar', 'user.external_id'],
    // scopeList: ['user.display_name'],
    handleResponseCallback() {
      snap.loginkit.fetchUserInfo().then(
        (result: any) => {
          resultCb(result)
        },
        (err: any) => {
          errCb(err)
        }
      )
    },
  })
}

export function initAppleLogin() {
  AppleID.auth.init({
    clientId: getPkgClientId(pkg).apple,
    scope: 'name email',
    redirectURI: window.location.href.split('?')[0],
    state: '[STATE]',
    usePopup: true,
  })
}

export function getDeeplinkHost() {
  if (getArgs('bundle_id') === 'com.sawa.weyayjackaroo') {
    return 'weyayplay'
  }
  let host = isInLudoClient ? 'sawaludo' : 'sawaksa'
  if (isInDiwaniaClient) {
    host = 'sawavideo'
  }
  return host
}

export function refreshAccount(accountResp: any, source: string) {
  const { identities, user_info } = accountResp.data

  if (user_info) {
    const { session_key, uid, icon_verify } = user_info
    const o = { session_key, uid, icon_verify }
    const v = encodeURIComponent(JSON.stringify(o))
    const host = getDeeplinkHost()
    window.location.href = `${host}://sawaksa_login/?sawaksa_acount=${v}&source=${source}`
  }
}

export function cancelAccount(key: string) {
  const host = getDeeplinkHost()
  const url = `${host}://unregister/?cancel_account_key=${key}`
  console.log('cancelAccount url:', url)
  window.location.href = url
}

export function unregisterRemind(data: any) {
  const host = getDeeplinkHost()
  window.location.href = `${host}://unregister_remind/?data=${encodeURIComponent(
    JSON.stringify(data)
  )}`
}

export function toFeedback() {
  reportEvent(Events.account_cancellation_verify_page_operation_result, {
    operation: '点击反馈入口',
  })
  const host = getDeeplinkHost()
  window.location.href = `${host}://feedback`
}

export async function report(eventName: string, fields = {}) {
  if (getPlatform().isIPhone) {
    const json = fields
    const host = getDeeplinkHost()
    window.location.href = `${host}://sawaksa_login_event/?eventName=${eventName}&jsonData=${btoa(
      encodeURIComponent(JSON.stringify(json))
    )}`
  } else {
    //
    let sa = getArgs('sa')
    if (sa) {
      sa = decodeURIComponent(sa)
      const isDebug = getArgs('is_debug')
      await initWebSensors(isDebug ? SensorSDKName.TEST : SensorSDKName.KSA, true, 'ajax', sa)
      reportWebSensors(eventName, fields)
    }
  }
}

export function getSub(access_token: string) {
  const arr = access_token.split('.')
  if (arr.length < 3) {
    return ''
  }
  try {
    const obj = arr[1]
    const subs = atob(obj)
    const o = JSON.parse(subs)
    const sub = o.sub
    return sub
  } catch (e) {
    return ''
  }
}

export function getSStorage(key: string) {
  try {
    return JSON.parse(sessionStorage.getItem(key) || '""')
  } catch (e) {
    return ''
  }
}

export function setSStorage(key: string, value: any) {
  sessionStorage.setItem(key, JSON.stringify(value))
}

export function getArgs(k: string) {
  const v = getArgsFromUrl(k)
  if (v) {
    setSStorage(k, v)
    return v
  } else {
    const _v = getSStorage(k)
    return _v
  }
}

export function getURLArgs(k: string) {
  const v = getArgsFromUrl(k)
  return v
}

