/* eslint-disable no-restricted-globals */
/* eslint-disable no-unused-expressions */
/* eslint-disable @typescript-eslint/ban-types */
import Vue, { PropType } from 'vue'
import { _T, textWithRTL } from '@badam/common-utils'

export interface DialogParam {
  title?: string
  content?: string
  confirmText?: string // 确认按钮的文案
  confirmExtraData?: any, // 透传数据
  confirmCallback?: Function // 确认按钮的事件
  cancelText?: string // 确认按钮的文案
  cancelCallback?: Function // 确认按钮的事件
}

export default Vue.extend({
  props: {
    item: Object as PropType<DialogParam>,
  },
  components: {},
  data() {
    return {}
  },
  // mounted() {},
  methods: {
    _T,
    textWithRTL,
    onConfirm() {
      if (this.item.confirmCallback) {
        this.item.confirmCallback(this.item.confirmExtraData || {})
      }
      this.close()
    },
    onCancel() {
      if (this.item.cancelCallback) {
        this.item.cancelCallback()
      }
      this.close()
    },
    close() {
      ;(this.$parent as any).show = false
    },
  },
})
