export function getSStorage(key: string) {
  try {
    return JSON.parse(sessionStorage.getItem(key) || '""')
  } catch (e) {
    return ''
  }
}

export function setSStorage(key: string, value: any) {
  sessionStorage.setItem(key, JSON.stringify(value))
}

export function getArgs(k: string) {
  const v = getArgsFromUrl(k)
  if (v) {
    setSStorage(k, v)
    return v
  } else {
    const _v = getSStorage(k)
    return _v
  }
}

export async function sleep(ts: number) {
  return new Promise((r) => {
    setTimeout(() => {
      r(true)
    }, ts)
  })
}
