import { api, ApiResult } from '@badam/common-utils'
import axios from 'axios'
import { getAuthType } from '@/src_unrg/utils'
import store from '../store'
import { getArgs, pkg } from '../utils'
// import qs from 'qs'
// import { ActivityRankingListRspMsg, CommonRankingListResult } from './interface'

api.setWgetEnable(false)

const wget = api.get.bind(api)
const get = api.get.bind(api)
const post = api.post.bind(api)

// axios.defaults.headers.post['content-type'] = 'application/x-www-form-urlencoded'

export function queryFbDeleteInfo(code: string) {
  return wget(
    '/api/user/query_fb_delete_info/',
    {
      code,
    },
    {
      'x-cli-br': 1,
    }
  ) as Promise<[boolean, ApiResult<any>]>
}

export function loginWithApple(
  user_id: string,
  access_token: string,
  nickname?: string,
  auto_create = false
) {
  const auth_access = store.state.data
  const uuid = getArgs('uuid') || ''
  const bundle_id = getArgs('bundle_id') || ''
  const obj: any = {
    login_type: 'apple',
    user_id,
    access_token,
    nickname,
    auth_type: 1,
    auth_access,
    auto_create,
  }
  let data: any = new URLSearchParams()
  obj && Object.keys(obj).forEach((key) => data.append(key, obj[key]))
  // data = qs.stringify(data)

  return post('/api/user_mode/get_login_key/', data, {
    'x-cli-br': 1,
    'x-cli-id': uuid,
    'x-cli-pn': bundle_id,
    'content-type': 'application/x-www-form-urlencoded',
  } as any) as Promise<[boolean, ApiResult<any>]>
}

