import Vue from 'vue'
import VueRouter, { RouteConfig } from 'vue-router'
import HomePage from '../views/home/index.vue'

Vue.use(VueRouter)

const routes: Array<RouteConfig> = [
  /// 注销功能 路由
  {
    path: '/',
    name: 'Home',
    component: HomePage,
  },
]

const router = new VueRouter({
  routes,
})

export default router
