// /* eslint-disable import/order */
// /* eslint-disable wrap-iife */
import Vue from 'vue'
import { getArgsFromUrl, getArgsFromUrlHash, matchExpr } from '@badam/common-utils'
import Qs from 'qs'
import { resolvePage, writePageToLS } from './utils'
import router from './src_ack/router/router'
import s1 from './src_ack/store'
import m1 from './src_ack/main.vue'
import { mainInit as ackInit } from './src_ack/init'
import { initDarkMode, initSawaSDK, loadVConsoleIfNeed } from './utils/func'
import { resolveThemeStyle } from './src_ack/utils'
import './src_ack/assets/css/page.scss'
import './src_ack/assets/css/page.pc.scss'
import { getConfigAndDial } from './utils/dial'
import FirebaseManager from './utils/firebase-manager'
import { setDefApiBase } from './src_unrg/api'

const isDev = getArgsFromUrlHash('is_debug')

async function main() {
  // const p = resolvePage()
  // console.warn('resolvePage', p)
  // writePageToLS(p)
  resolveThemeStyle()
  initSawaSDK()
  initDarkMode()
  if (!isDev) {
    // FirebaseManager域名替换，生产放开
    if ((process as any).env.VUE_APP_FLAVOR2 !== 'weyay') {
      try {
        await FirebaseManager.getInstance().init()
        await getConfigAndDial()
      } catch (err) {
        setDefApiBase()
        console.error(err)
      }
    } else {
      setDefApiBase()
    }
  } else {
    setDefApiBase()
  }
  const store = s1
  const Main = m1

  const q = Qs.parse(location.hash)
  const query: any = {}
  for (let k in q) {
    if (k.indexOf('#/') >= 0) {
      const key = k.replace('#/', '')
      query[key] = q[k]
    } else {
      query[k] = q[k]
    }
  }

  ackInit()

  loadVConsoleIfNeed()

  new Vue({
    store,
    router,
    render: (h) => h(Main),
  }).$mount('#app')
}

main()
