/* eslint-disable import/prefer-default-export */
import { getCurrentInstance, ref, watch, computed, onMounted, onUnmounted } from 'vue'

export function setup() {
  const CODE_SIZE = 24

  const code = ref('')
  const isFocus = ref(false)
  const isClear = ref(false)
  const codeChecked = computed(() => code.value.length === CODE_SIZE)

  async function onClear() {
    code.value = ''
    isFocus.value = false
    isClear.value = false
  }

  function onInputBlur() {
    isFocus.value = false
  }
  function onInputFocus() {
    isFocus.value = true
  }

  watch(
    () => code.value,
    (v) => {
      if (code.value && code.value.length > 0) {
        isClear.value = true
      }
      else {
        isClear.value = false
      }
    }
  )

  return {
    code,
    codeChecked,
    isFocus,
    isClear,
    onClear,
    onInputBlur,
    onInputFocus,
    CODE_SIZE,
  }
}
