/* eslint-disable import/prefer-default-export */

import { matchExpr } from '@badam/common-utils'
import { getArgs } from './src_unrg/utils'

export enum PageEnum {
  Unregister = 'unregister', // 默认值
  BindMobile = 'bind', // 绑手机号
  UnbindMobile = 'unbind', // 绑手机号
  UpdateMobile = 'update', // 绑手机号
}

// 用户注册包名
export enum PackageEnum {}

export function resolvePage() {
  const v = getArgs('page')
  console.warn('page arg:', v)
  if (v) {
    return matchExpr(
      v,
      [
        ['unregister', () => PageEnum.Unregister],
        ['bind', () => PageEnum.BindMobile],
        ['unbind', () => PageEnum.UnbindMobile],
        ['update', () => PageEnum.UpdateMobile],
      ],
      () => PageEnum.Unregister
    )
  }
  const u = localStorage.getItem('page')
  if (u) {
    return matchExpr(
      u,
      [
        ['unregister', () => PageEnum.Unregister],
        ['bind', () => PageEnum.BindMobile],
        ['unbind', () => PageEnum.UnbindMobile],
        ['update', () => PageEnum.UpdateMobile],
      ],
      () => PageEnum.Unregister
    )
  }
  return PageEnum.Unregister
}

export function writePageToLS(p: PageEnum) {
  const v = matchExpr(
    p,
    [
      [PageEnum.Unregister, 'unregister'],
      [PageEnum.BindMobile, 'bind'],
      [PageEnum.UnbindMobile, 'unbind'],
      [PageEnum.UpdateMobile, 'update'],
    ],
    () => undefined
  )
  if (v) {
    localStorage.setItem('page', v)
  }
}

export function getProduct() {
  const pkg = getArgs('package')
  if (pkg === 'com.sawa.weyay.h5') {
    return 'weyay'
  }
  return 'sawa'
}

export const CLIENT_ID =
  getProduct() === 'weyay'
    ? '155744833659-2ngdd6egv15433dt2i8etk6vf8eg21b3.apps.googleusercontent.com'
    : '532022495714-s5hikassju5bdbsmsfc277pt4ro2ff9l.apps.googleusercontent.com'
